import * as React from 'react';

import AddinPickerPane from './AddinPickerPane';
import AddinPickerStore from './AddinPickerStore';
import { IWorkBenchContext } from '../workBench/Workbench';

export default class ConnectedAddinPickerPane extends React.Component<void, void> {
  public static contextTypes: {} = {
    webPartStore: React.PropTypes.object.isRequired
  };

  // React will magically find this property and assign it based on WorkBench.getChildContext()
  // https://facebook.github.io/react/docs/context.html
  public context: IWorkBenchContext;

  private _store: AddinPickerStore;

  public render(): React.ReactElement<{}> {
    return <AddinPickerPane store={ this._store } />;
  }

  public componentWillMount(): void {
    this._store = new AddinPickerStore(this.context.webPartStore);
  }

  public componentWillUnmount(): void {
    // this._store.dispose();
    this._store = undefined;
  }
}
